@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 2px solid black;
  border-top: 2px double #f1356d;
  border-bottom-right-radius: 5%;
  border-bottom-left-radius: 5%;
  border-top-left-radius: 3%;
  border-top-right-radius: 3%;
}
.navbar h1 {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #f1356d;
}
.components {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}
.user-preview{
padding: 10px 16px;
margin: 20px 0;
border-bottom: 1px solid #fafafa;
}

.user-preview:hover{
box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}

.user-preview h2{
font-size: 20px;
color: #f1356d;
margin-bottom: 8px;
}